.work{
    display: flex;

    font-family: "Heebo";
    font-style: normal;
    font-weight: 300;
    line-height: 20px;
}

.infoWork{
    width: 400px;

    display: flex;
    flex-direction: column;
    gap: 10px; 

    font-family: "Heebo";
    font-style: normal;
    font-weight: 300;
    font-size: 17px;
    line-height: 20px;
}

.infoWork p{
    width: 400px;
    text-align: justify;
}

.education{
    display: flex;
}

.education p{
    width: 180px;
}

.infoEducation{
    width: 400px;

    display: flex;
    flex-direction: column;
    gap: 10px; 
}

.infoEducation p{
    width: 400px;
    
    text-align: justify;
    font-family: "Heebo";
    font-style: normal;
    font-weight: 300;
    line-height: 20px;
}
@media(max-width: 500px){
    .work,
    .education{
        flex-direction: column;
        gap:10px;
    }
    .education p{
        width: 400px;
    }
    .education p:last-child{
       padding-bottom: 20px;
    }
}

@media(max-width: 390px){
    .infoWork,
    .infoEducation,
    .infoWork p,
    .education p{
        width: 370px;
    }
}